<template>
    <div>
        <h4 class="mb-4">Базовый цвет:</h4>
        <photoshop-picker v-model="color" :presetColors="presetColors" @input="updateColor"></photoshop-picker>
    </div>
</template>

<script>
    import { Sketch } from 'vue-color'

    export default {
        components: {
            'photoshop-picker': Sketch
        },
        data(){
            return {
                color: { r: 0, g: 255, b: 0 },
                presetColors: [
                    'rgb(255, 255, 255)',
                    'rgb(255, 0, 0)',
                    'rgb(0, 255, 0)',
                    'rgb(0, 0, 255)',
                    'rgb(0, 255, 255)',
                    'rgb(255, 255, 0)',
                    'rgb(255, 0, 255)',
                ]
            }
        },
        methods: {
            updateColor(data){
                console.log(data);
                let color = data.rgba;
                delete color.a;
                this.$http.post('/settings/base-color', color)
            }
        }
    }
</script>

